<template>
	<view>
        <view class="bgwhite" style="padding: 0 24upx; margin-top: 20upx">
            <u-form ref="form1"  :model="formData" :rules="rules" labelPosition="left"  :labelStyle="{fontSize:'28upx',color:'#1a1a1a'}" errorType="toast">
                <u-form-item :label="$t('原密码')+':'" borderBottom labelWidth="160upx" prop="oldPassword">
                    <u-input v-model="formData.oldPassword" password :placeholder="$t('请输入原密码')"  border="none" placeholderClass="placeholderClass"></u-input>
                </u-form-item>
                <u-form-item :label="$t('新密码')+':'" borderBottom labelWidth="160upx" prop="password">
                    <u-input v-model="formData.password" password :placeholder="$t('请输入新密码')"  border="none" placeholderClass="placeholderClass"></u-input>
                </u-form-item>
                <u-form-item :label="$t('确认密码')+':'" borderBottom labelWidth="160upx" prop="confirmPassword">
                    <u-input v-model="formData.confirmPassword" password :placeholder="$t('请输入确认密码')"  border="none" placeholderClass="placeholderClass"></u-input>
                </u-form-item>
            </u-form>
        </view>


        <view class="btnWrap" style="margin-top: 100upx; padding: 0 90upx">
            <u-button :text="$t('保存')"  :loading="buttonLoading" :loadingText="$t('保存中')" class="yewlloBtn" shape="circle" :color="$config.color.primary" @click="submit"></u-button>
        </view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
                buttonLoading: false,
                formData:{
                    oldPassword:"",
                    password:"",
                    confirmPassword:""
                },
                rules:{
                    oldPassword: [
                        {required: true, message: this.$t('请输入原密码')},
                        {min:6, message: this.$t("密码不能小于6位字符")},
                    ],
                    password: [
                        {required: true, message: this.$t('请输入新密码')},
                        {min:6, message: this.$t("密码不能小于6位字符")},
                        { message: this.$t('新密码与确认密码不一至'),  validator: this.validPwd }
                    ],
                    confirmPassword: [
                        {required: true, message: this.$t('请输入确认密码')},
                        {min:6, message: this.$t("密码不能小于6位字符")},
                        { message: this.$t('新密码与确认密码不一至'),  validator: this.validPwd }
                    ]
                }
			};
		},
        methods:{
            submit(){
                this.buttonLoading = true;
                this.$refs.form1.validate().then(() => {

                })
            }
        }
	}
</script>

